'use strict';

import * as d3 from 'd3';

export const ellipsisText = (text, width, xOffset) => {
  text.each(function () {
    const text = d3.select(this);
    const words = text.text().split(/\s+/);
    const ellipsis = text
      .text('')
      .append('tspan')
      .attr('class', 'elip')
      .text('...');
    if (xOffset !== undefined) { ellipsis.attr('transform', () => xOffset ? `translate(${xOffset}, 0)` : null); }
    const availableWidth = width - ellipsis.node().getComputedTextLength();
    const numWords = words.length;
    const tspan = text.insert('tspan', ':first-child').text(words.join(' '));

    // Try the whole line
    // While it's too long, and we have words left, keep removing words
    while (tspan.node().getComputedTextLength() > availableWidth && words.length) {
      words.pop();
      tspan.text(words.join(' '));
    }

    if (words.length === numWords) {
      ellipsis.remove();
    }
  });
};

const Utils = {
  ellipsisText: ellipsisText
};

export const escapeSpecialChars = (jsonString) => {
  let output = jsonString.replaceAll('\n', '^^n');
  output = output.replaceAll('\r', '^^r');
  return output;
};

export default Utils;
